/**
 * ファウンデーションレイヤー
 * 変数とミックスイン
 */
:root {
  --color-primary: #ff006c;
  --color-text: #494b67;
  --color-bg: #fff;
  --color-bg-light: #d5f7ff;
  --color-bg-lighter: #ecebff;
  --color-border: #ecebff;
  --color-card-bg: rgba(255, 255, 255, 0.8);
  --color-card-border: rgba(255, 255, 255, 0.5);
}

[data-theme=dark] {
  --color-primary: #ff2a82;
  --color-text: #eeeef5;
  --color-bg: #1B1B23;
  --color-bg-light: #2D2D3A;
  --color-bg-lighter: #343446;
  --color-border: #343446;
  --color-card-bg: rgba(30, 30, 40, 0.8);
  --color-card-border: rgba(255, 255, 255, 0.1);
}

body {
  background-color: var(--color-bg);
  color: var(--color-text);
  transition: background-color 0.3s ease, color 0.3s ease;
}

.dud {
  color: var(--color-primary);
  opacity: 0.7;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}
@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
}

body {
  font-family: "Noto Sans JP", sans-serif;
  color: #494b67;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}
a:focus-visible {
  outline: 2px solid rgba(255, 0, 108, 0.5);
  outline-offset: 3px;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  font-family: inherit;
}
button:focus-visible {
  outline: 2px solid rgba(255, 0, 108, 0.4);
  outline-offset: 3px;
}

input,
textarea,
select {
  font-family: inherit;
}

