/**
 * フォントコンポーネント
 */
@font-face {
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 500;
  src: local("Noto Sans JP"), url("./fonts/NotoSansJP-Regular.woff2") format("woff2");
  font-display: swap;
}
/**
 * レイアウトレイヤー
 * サイト全体の骨組みを定義
 */
/**
 * コンテナレイアウト
 * 最大幅と中央揃えを管理
 */
.l-container {
  width: 100%;
  max-width: 72rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
@media (min-width: 768px) {
  .l-container {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}
.l-container--narrow {
  max-width: 64rem;
}

/**
 * グリッドレイアウト
 * レスポンシブグリッドシステム
 */
.l-grid {
  display: grid;
  gap: 1.5rem;
}
.l-grid--cols-1 {
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .l-grid--cols-2 {
    grid-template-columns: repeat(2, 1fr);
  }
}
.l-grid--cols-3 {
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .l-grid--cols-3 {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .l-grid--cols-3 {
    grid-template-columns: repeat(3, 1fr);
  }
}
@supports (display: masonry) {
  .l-grid--cols-3 {
    display: masonry;
    masonry-auto-flow: next;
  }
}
@supports (grid-template-rows: masonry) {
  .l-grid--cols-3 {
    grid-template-rows: masonry;
  }
}

/**
 * メインスタイルシート
 * PDFLOCSSの順序で各レイヤーをインポート
 */
/**
 * コンポーネントレイヤー
 * 再利用可能なUI要素
 */
/**
 * バッジコンポーネント
 * タグやラベル表示用
 */
.c-badge {
  display: inline-block;
  padding: 0.5rem 0.75rem;
  background-color: #ecebff;
  color: #494b67;
  font-size: 1rem;
  border-radius: 99em;
  line-height: 1.25;
  transition: all 0.3s ease;
}
.c-badge:is(button):hover {
  background-color: #ff006c;
  color: #fff;
}
.c-badge[popovertarget] {
  cursor: pointer;
}
.c-badge--small {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
}

/**
 * ボタンコンポーネント
 * 再利用可能なボタンUI
 */
.c-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 2rem;
  border: none;
  border-radius: 99em;
  font-weight: 700;
  cursor: pointer;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}
.c-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}
.c-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}
.c-button:hover::before {
  opacity: 1;
}
.c-button:active {
  transform: translateY(0);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}
.c-button--primary {
  background: linear-gradient(135deg, #ff006c, rgb(255, 51, 137.4));
  color: #fff;
}
.c-button--primary:hover {
  background: linear-gradient(135deg, rgb(255, 25.5, 122.7), rgb(255, 76.5, 152.1));
}
.c-button--outline {
  background: transparent;
  border: 2px solid #ff006c;
  color: #ff006c;
  box-shadow: none;
}
.c-button--outline:hover {
  background: #ff006c;
  color: #fff;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}
.c-button--secondary {
  background: #ecebff;
  color: #494b67;
}
.c-button--secondary:hover {
  background: rgb(211.775, 209.5, 255);
}

/**
 * カードコンポーネント
 * 再利用可能なカードUI
 */
.c-card, .p-portfolio__skill-card, .p-portfolio__work-card, .p-portfolio__note-card {
  background: var(--color-card-bg);
  backdrop-filter: blur(10px);
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 0px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid var(--color-card-border);
}
@media (hover: hover) {
  .c-card, .p-portfolio__skill-card, .p-portfolio__work-card, .p-portfolio__note-card {
    transition: all 0.3s ease;
  }
}
.c-card:hover, .p-portfolio__skill-card:hover, .p-portfolio__work-card:hover, .p-portfolio__note-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  background: var(--color-bg);
}

.c-cursor {
  position: fixed;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  background-color: rgba(255, 0, 108, 0.5);
  border: 1px solid #ff006c;
  border-radius: 50%;
  pointer-events: none;
  z-index: 9999;
  transform: translate3d(0, 0, 0);
  margin-left: -10px;
  margin-top: -10px;
  transition: width 0.3s ease, height 0.3s ease, background-color 0.3s ease, margin 0.3s ease;
  opacity: 0;
}
.c-cursor.is-active {
  opacity: 1;
}
.c-cursor.is-hover {
  width: 50px;
  height: 50px;
  margin-left: -25px;
  margin-top: -25px;
  background-color: rgba(255, 0, 108, 0.1);
  border-color: transparent;
  mix-blend-mode: difference;
}

@media (hover: none) and (pointer: coarse) {
  .c-cursor {
    display: none;
  }
}
/**
 * popover
 * 主にツールチップ用（吹き出し + アニメーション）
 */
/* popover 機能を使う要素の初期設定 */
[popover] {
  border: none;
  background: transparent;
  overflow: visible;
  padding: 0;
  transition: display 0.25s allow-discrete, overlay 0.25s allow-discrete;
}

/* ---------------------------------------------------------
popover 本体（吹き出しボックス）
<div class="c-popbox" popover id="...">
--------------------------------------------------------- */
.c-popbox {
  position: absolute;
  position-area: top span-right;
  margin: 0.85em 0;
  max-width: 20rem;
  width: max-content;
  background-color: rgba(20, 20, 30, 0.6);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.25rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.2), 0 2px 4px -1px rgba(0, 0, 0, 0.06), 0 0 0 1px rgba(255, 255, 255, 0.05) inset;
  /* テキスト */
  color: #fff;
  padding: 0.5rem;
  font-size: 0.9em;
  line-height: 1.625;
  /* 初期状態（非表示時） */
  opacity: 0;
  transform: translateY(0) scale(0.8);
  /* 表示時のアニメーション */
}
.c-popbox:popover-open {
  opacity: 1;
  transform: translateY(0) scale(1);
  animation: popover-fade-in 0.2s ease;
  transition: display 0.2s allow-discrete;
}
.c-popbox {
  /* 閉じる時はアニメーションなし（即座に非表示） */
}
.c-popbox:not(:popover-open) {
  opacity: 0;
  transform: translateY(0) scale(0.8);
  transition: none;
}
.c-popbox {
  /* アニメーション定義（開く時のみ） */
}
@keyframes popover-fade-in {
  from {
    opacity: 0;
    transform: translateY(0) scale(0.8);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
.c-popbox::before {
  content: "";
  position: absolute;
  display: block;
  width: 1.25em;
  height: 0.6em;
  left: 1em;
  bottom: -0.6em;
  clip-path: polygon(0 0, 100% 0%, 50% 100%);
  background-color: rgba(20, 20, 30, 0.6);
}

/* ---------------------------------------------------------
popover トリガーとなるバッジ（?マーク付きボタン）
<button class="c-badge c-badge--popover" popovertarget="...">
--------------------------------------------------------- */
.c-badge[popovertarget] {
  position: relative;
  padding-right: 2em;
  /* 常時表示される “?” マーク */
}
.c-badge[popovertarget]::after {
  content: "?";
  display: inline-block;
  text-align: center;
  position: absolute;
  bottom: 0.5rem;
  width: 1.2rem;
  height: 1.2rem;
  line-height: 1.4;
  margin-left: 0.25em;
  background-color: #494b67;
  border-radius: 99em;
  font-family: "Pixelify Sans", "Noto Sans JP", sans-serif;
  font-size: 0.9em;
  color: #fff;
}
.c-badge[popovertarget].c-badge--popover:has(+ .c-popbox:popover-open) {
  background-position: right center;
  background-size: 200% auto;
  animation: ripple 1.5s 1;
}
@keyframes ripple {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 0, 108, 0.3);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
  }
}

/**
 * スライダーコンポーネント
 *
 * 概要:
 * 画像スライダーコンポーネントのスタイル定義
 *
 * 主な仕様:
 * - 横スクロール型のスライダー
 * - 前へ/次へボタンによる操作
 * - ページネーション表示
 * - レスポンシブ対応
 *
 * 制限事項:
 * - スライドは横方向のみ対応
 */
.c-slider {
  position: relative;
  width: 100%;
  margin-bottom: 1rem;
}
.c-slider__viewport {
  position: relative;
  width: 100%;
  overflow: hidden;
  border-radius: 0.25rem;
}
.c-slider__track {
  display: flex;
  transition: transform 0.3s ease-in-out;
  will-change: transform;
}
.c-slider__slide {
  flex: 0 0 100%;
  width: 100%;
  position: relative;
}
.c-slider__slide img {
  aspect-ratio: 4/3;
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}
.c-slider__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.9);
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 0;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}
.c-slider__button:hover, .c-slider__button:focus-visible {
  background-color: #fff;
  border-color: rgba(73, 75, 103, 0.2);
  outline: 2px solid #ff006c;
  outline-offset: 0;
}
.c-slider__button:active {
  background-color: rgba(255, 255, 255, 0.8);
}
.c-slider__button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.c-slider__button svg {
  width: 24px;
  height: 24px;
  color: #494b67;
  pointer-events: none;
}
.c-slider__button--prev {
  left: 0;
}
.c-slider__button--next {
  right: 0;
}
@media (min-width: 640px) {
  .c-slider__button {
    width: 30px;
    height: 30px;
  }
  .c-slider__button svg {
    width: 24px;
    height: 24px;
  }
}
.c-slider__pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-top: 1rem;
  flex-wrap: wrap;
}
.c-slider__pagination-button {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: none;
  background-color: rgba(255, 255, 255, 0.3);
  cursor: pointer;
  padding: 0;
  transition: background-color 0.3s ease, transform 0.3s ease;
}
.c-slider__pagination-button:hover, .c-slider__pagination-button:focus-visible {
  background-color: rgba(255, 255, 255, 0.5);
  outline: 2px solid #ff006c;
  outline-offset: 2px;
  transform: scale(1.2);
}
.c-slider__pagination-button.is-active {
  background-color: #ff006c;
