  border: none;
  cursor: pointer;
  padding: 0.5rem;
  color: var(--color-text);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 99em;
  transition: background-color 0.3s ease, color 0.3s ease;
  position: relative;
  overflow: hidden;
  width: 2.5rem;
  height: 2.5rem;
}
.c-theme-toggle:hover {
